import request, { Method } from '@/utils/request'

/**
 * 
 */
export function uploadAvatarURL() {
  return 'http://127.0.0.1:8001/common/upload/avatar';
} 
export function uploadAvatar (params) {
  return request({
    url: '/common/upload/avatar',
    method: Method.POST,
    needToken: true,
    data: params,

  });
}

/**
 * 
 */
export function uploadWikiCoverURL() {
  return 'http://127.0.0.1:8001/common/upload/wiki-cover';
} 
export function uploadWikiCover (params) {
  return request({
    url: '/common/upload/wiki-cover',
    method: Method.POST,
    needToken: true,
    data: params,

  });
}


/**
 * 
 */
export function uploadWikiContentURL() {
  return 'http://127.0.0.1:8001/common/upload/wiki-cover';
} 
export function uploadWikiContentImage (params) {
  return request({
    url: '/common/upload/wiki-content',
    method: Method.POST,
    needToken: true,
    data: params,
    headers: { 'Content-Type': 'multipart/form-data' },
  });
}

